import { Navigate } from "react-router-dom";
import { ProtectedRouteProps } from "../components/interfaces/ProtectedRoutes";
import { AuthScreen } from "../components/auth/AuthScreen";

export const PublicRoutes = ({ isAuthenticated }: ProtectedRouteProps) => {
    if (!isAuthenticated) {
        return <AuthScreen />;
    } else return <Navigate to="/" />;
};
