import { Navigate } from "react-router-dom";
import { CalendarScreen } from "../components/calendar/CalendarScreen";
import { ProtectedRouteProps } from "../components/interfaces/ProtectedRoutes";

export const PrivateRoutes = ({ isAuthenticated }: ProtectedRouteProps) => {
    if (isAuthenticated) {
        return <CalendarScreen />;
    } else return <Navigate to="/auth" />;
};
