import Swal from "sweetalert2";
import { AppThunk } from "../app/store";
import { Event } from "../components/interfaces/event";
import { fetchWithToken } from "../helpers/fetch";
import { prepareEvents } from "../helpers/prepare-events";
import { types } from "../types/types";

export const eventStartAddNew = (event: Event): AppThunk => {
    return async (dispatch, getState) => {
        try {
            const { uid, name } = getState().auth;
            const resp = await fetchWithToken("events/new", event, "POST");
            const body = await resp.json();
            console.log(body);

            if (body.ok) {
                event.id = body.event.id;
                event.uid = { _id: uid, name };
                dispatch(eventAddNew(event));
            }
        } catch (error) {
            console.log(error);
        }
    };
};

const eventAddNew = (event: Event) => ({
    type: types.eventAddNew,
    payload: event,
});

export const setActive = (event: Event) => ({
    type: types.eventSetActive,
    payload: event,
});

export const eventClearActiveEvent = () => ({
    type: types.eventClearActiveEvent,
});

export const eventStartUpdate = (event: Event): AppThunk => {
    return async (dispatch) => {
        try {
            const resp = await fetchWithToken(
                `events/${event.id}`,
                event,
                "PUT"
            );
            const body = await resp.json();

            if (body.ok) {
                dispatch(eventUpdate(event));
            } else {
                Swal.fire("Error", body.msg, "error");
            }
        } catch (error) {
            console.log(error);
        }
    };
};

const eventUpdate = (event: Event) => ({
    type: types.eventUpdate,
    payload: event,
});

export const eventClear = () => ({
    type: types.eventClear,
});

export const eventStartDelete = (): AppThunk => {
    return async (dispatch, getState) => {
        const { id } = getState().calendar.activeEvent;
        try {
            const resp = await fetchWithToken(`events/${id}`, {}, "DELETE");
            const body = await resp.json();
            console.log(body);
            if (body.ok) {
                dispatch(eventDelete());
            } else {
                Swal.fire("Error", body.msg, "error");
            }
        } catch (error) {
            console.log(error);
        }
    };
};

const eventDelete = () => ({
    type: types.eventDelete,
});

export const eventStartLoading = (): AppThunk => {
    return async (dispatch) => {
        try {
            const resp = await fetchWithToken("events", {});
            const body = await resp.json();
            console.log(body);
            const events = prepareEvents(body.events);
            console.log(events);
            dispatch(eventLoaded(events));
        } catch (error) {
            console.log(error);
        }
    };
};

const eventLoaded = (events: any) => ({
    type: types.eventLoader,
    payload: events,
});
