import { types } from "../types/types";

interface Action {
    type: string;
    payload?: {
        uid: String;
        name: String;
    };
}

interface State {
    checking: boolean;
    uid?: String;
    name?: String;
}

const initialState = {
    checking: true,
};

export const authReducer = (state: State = initialState, action: Action) => {
    switch (action.type) {
        case types.authLogin:
            return {
                ...state,
                checking: false,
                ...action.payload,
            };
        case types.authCheckingFinish:
            return {
                ...state,
                checking: false,
            };
        case types.authLogout:
            return {
                checking: false,
            };
        default:
            return state;
    }
};
