import React, { SyntheticEvent } from "react";
// import { useDispatch } from "react-redux";
import Swal from "sweetalert2";
import { startLogin, startRegister } from "../../actions/auth";
import { useAppDispatch } from "../../app/hooks";
// import { startLogin, startRegister } from "../../actions/auth";
import { useForm } from "../hooks/useForm";
import { NewUser, User } from "../interfaces/auth";
// import "./login.css";

const initialRegisterState = {
    rEmail: "",
    rPassword: "",
    rPassword2: "",
    rName: "",
};

const initialLoginState = {
    lEmail: "",
    lPassword: "",
};

export const AuthScreen = () => {
    const dispatch = useAppDispatch();

    const {
        values: formLoginValues,
        handleInputChange: handleLoginInputChange,
    } = useForm<User>(initialLoginState);

    const {
        values: formRegisterValues,
        handleInputChange: handleRegisterInputChange,
    } = useForm<NewUser>(initialRegisterState);

    const handleLogin = (e: SyntheticEvent) => {
        e.preventDefault();
        dispatch(startLogin(lEmail, lPassword));
    };

    const handleRegister = (e: SyntheticEvent) => {
        e.preventDefault();

        if (rPassword !== rPassword2) {
            return Swal.fire(
                "Error",
                "Las contraseñas deben de ser iguales",
                "error"
            );
        }
        dispatch(startRegister(rEmail, rPassword, rName));
    };

    const { lEmail, lPassword } = formLoginValues;
    const { rEmail, rPassword, rPassword2, rName } = formRegisterValues;

    return (
        <div className="container login-container">
            <div className="row">
                <div className="col-md-6 login-form-1">
                    <h3>Ingreso</h3>
                    <form onSubmit={handleLogin}>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Correo"
                                name="lEmail"
                                value={lEmail}
                                onChange={handleLoginInputChange}
                                autoComplete="off"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contraseña"
                                name="lPassword"
                                value={lPassword}
                                onChange={handleLoginInputChange}
                                autoComplete="off"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="submit"
                                className="btnSubmit"
                                value="Login"
                            />
                        </div>
                    </form>
                </div>

                <div className="col-md-6 login-form-2">
                    <h3>Registro</h3>
                    <form onSubmit={handleRegister}>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Nombre"
                                name="rName"
                                value={rName}
                                onChange={handleRegisterInputChange}
                                autoComplete="off"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Correo"
                                name="rEmail"
                                value={rEmail}
                                onChange={handleRegisterInputChange}
                                autoComplete="off"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contraseña"
                                name="rPassword"
                                value={rPassword}
                                onChange={handleRegisterInputChange}
                                autoComplete="off"
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Repita la contraseña"
                                name="rPassword2"
                                value={rPassword2}
                                onChange={handleRegisterInputChange}
                                autoComplete="off"
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="submit"
                                className="btnSubmit"
                                value="Crear cuenta"
                            />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};
