import Swal from "sweetalert2";
import { AppThunk } from "../app/store";
import { LoggedUser } from "../components/interfaces/auth";
import { fetchWithToken, fetchNoToken } from "../helpers/fetch";
import { types } from "../types/types";

export const startLogin =
    (email: string, password: string): AppThunk =>
    async (dispatch) => {
        const resp = await fetchNoToken("auth", { email, password }, "POST");
        const body = await resp.json();

        if (body.ok) {
            localStorage.setItem("token", body.token);
            localStorage.setItem(
                "token-init-date",
                new Date().getTime().toString()
            );
            dispatch(
                login({
                    uid: body.uid,
                    name: body.name,
                })
            );
        } else {
            Swal.fire("Error", body.msg, "error");
        }
    };

export const startRegister = (
    email: string,
    password: string,
    name: string
): AppThunk => {
    return async (dispatch) => {
        const resp = await fetchNoToken(
            "auth/new",
            { email, password, name },
            "POST"
        );
        const body = await resp.json();
        if (body.ok) {
            localStorage.setItem("token", body.token);
            localStorage.setItem(
                "token-init-date",
                new Date().getTime().toString()
            );
            dispatch(
                login({
                    uid: body.uid,
                    name: body.name,
                })
            );
        } else {
            Swal.fire("Error", body.msg, "error");
        }
    };
};

export const startChecking = (): AppThunk => async (dispatch) => {
    const resp = await fetchWithToken("auth/renew", {});
    const body = await resp.json();
    if (body.ok) {
        localStorage.setItem("token", body.token);
        localStorage.setItem(
            "token-init-date",
            new Date().getTime().toString()
        );
        dispatch(
            login({
                uid: body.uid,
                name: body.name,
            })
        );
    } else {
        dispatch(checkingFinish());
    }
};

const checkingFinish = () => ({ type: types.authCheckingFinish });

const login = (user: LoggedUser) => ({
    type: types.authLogin,
    payload: user,
});

export const startLogout = (): AppThunk => (dispatch) => {
    localStorage.clear();
    dispatch(logout());
};

const logout = () => ({ type: types.authLogout });
